.course-container{width:90%}.course-container .collapsible{background-color:rgba(0,0,0,0);color:#444;cursor:pointer;padding:18px;width:100%;border:none;text-align:left;outline:none;font-size:15px;font-family:"Noto Serif",serif}.course-container .active,.course-container .collapsible:hover{background-color:#ccc}.course-container .collapsible:after{content:"+";color:#000;font-weight:bold;float:right;margin-left:5px}.course-container .active:after{content:"−"}.course-container .content{padding:0 18px;overflow:hidden;background-color:#f1f1f1}